.logo {
  position: absolute;
  width: 106px;
  height: 90px;
  left: 3px;
  top: 3px;

@include for-size(tablet-up) {
  width: 179px;
  height: 151px;
}

@include for-size(desktop-up){
  width: 239px;
  height: 201px;
}

